export var SHOW_SCHEMA_MODAL = 'SHOW_SCHEMA_MODAL';
export var HIDE_SCHEMA_MODAL = 'HIDE_SCHEMA_MODAL';
export function showSchemaModal() {
    return {
        type: SHOW_SCHEMA_MODAL,
    };
}
export function hideSchemaModal() {
    return {
        type: HIDE_SCHEMA_MODAL,
    };
}
export var CHANGE_NOT_APPLIED_ACTIVE_PRESET = 'CHANGE_NOT_APPLIED_ACTIVE_PRESET';
export var CHANGE_NOT_APPLIED_DISPLAY_OPTIONS = 'CHANGE_NOT_APPLIED_DISPLAY_OPTIONS';
export var CHANGE_ACTIVE_PRESET = 'CHANGE_ACTIVE_PRESET';
export function changeNaActivePreset(value, schema) {
    return {
        type: CHANGE_NOT_APPLIED_ACTIVE_PRESET,
        payload: {
            presetName: value,
            schema: schema,
        },
    };
}
export function changeNaDisplayOptions(options) {
    return {
        type: CHANGE_NOT_APPLIED_DISPLAY_OPTIONS,
        payload: options,
    };
}
export function changeActivePreset(preset) {
    console.log(preset);
    return {
        type: CHANGE_ACTIVE_PRESET,
        payload: preset,
    };
}



